.card__link {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif; }
  .card__link h3 {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px; }

.footer {
  background-color: #041827;
  color: rgba(255, 255, 255, 0.7);
  padding-top: 50px;
  padding-bottom: 55px; }

.footer__container {
  display: grid;
  grid-template-columns: 215px 1fr 345px; }
  @media (max-width: 1023px) {
    .footer__container {
      grid-template-columns: 1fr 47%; } }
  @media (max-width: 767px) {
    .footer__container {
      grid-template-columns: 1fr; } }

.footer__social {
  grid-column-start: 1; }
  @media (max-width: 1023px) {
    .footer__social {
      grid-column-start: 1;
      grid-column-end: 3; } }
  @media (max-width: 767px) {
    .footer__social {
      grid-column-start: 1;
      grid-column-end: 2; } }

.footer__nav {
  grid-column-start: 2;
  grid-column-end: 3;
  align-self: end;
  justify-self: end;
  margin-left: 30px; }
  @media (max-width: 1023px) {
    .footer__nav {
      grid-column-start: 1;
      grid-column-end: 2; } }
  @media (max-width: 767px) {
    .footer__nav {
      grid-column-start: 1;
      grid-column-end: 2;
      justify-self: stretch;
      margin-left: 0; } }

.footer__address {
  grid-column-start: 3; }
  @media (max-width: 1023px) {
    .footer__address {
      grid-column-start: 2;
      grid-column-end: 3; } }
  @media (max-width: 767px) {
    .footer__address {
      grid-column-start: 1;
      grid-column-end: 2; } }

.social {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  position: relative; }
  @media (max-width: 1023px) {
    .social {
      grid-template-columns: 215px 1fr;
      grid-template-rows: min-content; } }
  @media (max-width: 767px) {
    .social {
      grid-template-columns: 1fr;
      justify-items: center; }
      .social::after {
        display: block;
        position: absolute;
        content: "";
        width: 100vw;
        height: 1px;
        background-color: rgba(242, 245, 246, 0.7);
        bottom: 0;
        left: -25px; } }

.social__logo {
  margin-bottom: 30px; }
  .social__logo img {
    display: block; }

.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-self: end; }
  @media (max-width: 767px) {
    .social__list {
      justify-self: center; } }

.social__item {
  margin-right: 15px; }

.social__link {
  display: block; }

.main-nav {
  position: relative; }
  .main-nav h2 {
    text-transform: uppercase;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 16px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 20px;
    padding: 0; }
    @media (max-width: 767px) {
      .main-nav h2 {
        margin-top: 20px;
        margin-bottom: 15px; } }
  @media (max-width: 767px) {
    .main-nav::after {
      display: block;
      position: absolute;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(242, 245, 246, 0.7);
      bottom: 0;
      left: -25px; } }

.main-nav__toggle {
  position: absolute;
  right: 0;
  top: 20px; }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: minmax(min-content, 250px) minmax(min-content, 250px); }
  @media (max-width: 1023px) {
    .main-nav__list {
      display: flex;
      flex-wrap: wrap; } }

.main-nav__column {
  list-style: none;
  margin: 0;
  padding: 0; }

.main-nav__item {
  margin-bottom: 10px;
  margin-right: 30px; }
  @media (max-width: 1023px) {
    .main-nav__item {
      max-width: 220px; } }

.main-nav__link {
  color: rgba(255, 255, 255, 0.7); }

.address {
  position: relative; }
  @media (max-width: 767px) {
    .address::after {
      display: block;
      position: absolute;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(242, 245, 246, 0.7);
      bottom: 0;
      left: -25px; } }
  .address h2 {
    text-transform: uppercase;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 16px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 20px;
    padding: 0; }
    @media (max-width: 767px) {
      .address h2 {
        margin-top: 20px;
        margin-bottom: 15px; } }
  .address p {
    margin: 0;
    margin-bottom: 10px;
    padding: 0; }
  .address a {
    color: rgba(255, 255, 255, 0.7); }

.address__toggle {
  position: absolute;
  right: 0;
  top: 20px; }

